import React, {useEffect, useState, useContext} from 'react'
import axios from 'axios'
import Badge from './Badge'
import { formContext } from '../context/formContext'
const Select = ({type}) => {
    const url = `https://www.thecocktaildb.com/api/json/v1/1/list.php?${type}=list`
    const {submited, setcategorys,setingredients, setsubmited} = useContext(formContext)
    const [options, setOptions] = useState([])
    const [selected, setSelected] = useState([])
    const [selectType, setSelect] = useState({
        title:'',
        color:'',
        text:''
    })
    useEffect(()=> {
        if(submited){
            type==='c' ? setcategorys(selected) : setingredients(selected)
        }

        type==='c'?setSelect({title: 'My Alcohols', color:'bg-blue-600', text:'Seleccione Categorias'}) :setSelect({title: 'My Ingredients', color:'bg-red-500', text:'Seleccione Ingredientes'})


        const fetchItems = async () =>{
            const response = await axios.get(url);
            setOptions(response.data.drinks.map(obj => Object.values(obj)[0]))
        }
        fetchItems();

        
 // eslint-disable-next-line react-hooks/exhaustive-deps
    },[type, url, submited])

    const handleChange = (e) =>{
        e.preventDefault()
        if(selected.includes(e.target.value) || e.target.value==='')return;
        setSelected([...selected, e.target.value])
    } 
    return ( 
        <div className="my-8">
            <h1 className="text-xl m-5 font-light ">{selectType.title}</h1>
            <select className="cursor-pointer block  w-56 px-4 py-2  rounded-md " onChange={(e)=>handleChange(e)}name="" id="">
                <option value="">{selectType.text}</option>
                    {options ? 
                        options.map(option => (
                        <option className="font-medium" key={option} value={option}>{option}</option> 
                        )):
                        null}

            </select>

            <div className="my-4 grid grid-cols-2 gap-2">

                {selected?
                    selected.map(item =>  <Badge selected={selected} setSelected={setSelected} key={item} type={type} color={selectType.color} name={item} />)
                    :
                    null
                }
            </div>
        </div>
     );
}
 
export default Select;