import React from 'react'




const Badge = ({selected, setSelected, type, color, name}) => {

    const handleClick = (e) => {
        setSelected(selected.filter(item => name !== item))
    }
    return ( 
        <span onClick={handleClick} className={`w-10/12 cursor-pointer text-sm text-white rounded-md ${color} px-3 py-0.5`}>X {name}</span>
     );
}

 
export default Badge;