import React, {useState, createContext, useEffect, useContext} from 'react'
import axios from 'axios'
import { drinksContext } from './drinksContext'
export const formContext = createContext()
const FormProvider = (props) => {
    const {setdrinks} = useContext(drinksContext)
    const [categorys, setcategorys]  = useState([])
    const [ingredients, setingredients]  = useState([])
    const [submited, setsubmited] = useState(false)
    const [alcohol, setalcohol] = useState(true)
    const [apromises, setpromises] = useState([])

    useEffect(()=> {
        if(!submited)return
        let urls = []
        const fetchDrinks = () => {
            ingredients.forEach(ingredient => {
                console.log("hola1")

                categorys.forEach(category => {
            console.log("hola2")

                   const temp = `https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=${category}&i=${ingredient}`
                   urls = [...urls, temp]                    
                })
            })
            
        }
            fetchDrinks();
            setpromises(urls.map(url => axios.get(url)))
            Promise.all(apromises).then(
                result =>{
                    console.log(result)
                    const temp = result.map(( el) => el.data.drinks).
                        reduce((ac, el)=> {
                            console.log(el)
                            el.forEach(drink => {
                                ac = [...ac, drink]
                            })
                            return ac
                    },[])
                    setdrinks(temp)
                }
            )
        setsubmited(!submited)
    },[submited])
    return (
        <formContext.Provider value={{alcohol, submited,categorys, ingredients, setcategorys,setingredients, setsubmited, setalcohol}}>
            {props.children}
        </formContext.Provider>
      );
}
 
export default FormProvider;
