import React, {useContext} from 'react'
import Select from './Select'
import { formContext } from '../context/formContext';
const Form = ({type}) => {
    const ingredientSelect = 'i';
    const categorySelect = 'c';
    const {alcohol, setsubmited , setalcohol} = useContext(formContext)
    const handleSubmit = (e) =>{
        e.preventDefault();
        setsubmited(true)
    }
    return ( 
        <div className="bg-gray-100 h-full p-8">
            
            <form onSubmit={(e)=>handleSubmit(e)}>

            <div >   
                <Select type={ingredientSelect}/>
                <Select type={categorySelect}/>

                    
            </div>

            <div>
                <div>
                    <input onChange={()=> setalcohol(!alcohol)} type="radio" name="alcohol" value={1} defaultChecked />
                    <span className="mx-12">With Alcohol</span>
                </div>
                <div>
                    <input onChange={()=> setalcohol(!alcohol)}  type="radio" name="alcohol" value={0}/>
                    <span className="mx-12">Without Alcohol</span>
                </div>
            </div>

            <input className="block w-11/12 cursor-pointer bg-blue-900 text-white rounded px-4 py-1.5 my-8" type="submit" value="Find your recipe" />
            </form>
           
        </div>
     );
}
 
export default Form;