import React from 'react'
import styled from '@emotion/styled'
const CardBody =  styled.div`
    min-width: 180px;
    max-width: 270px;
`;
const Drink = ({drink}) => {
    const {idDrink, strDrink, strDrinkThumb} = drink
    return ( 
        <div>
            <CardBody className="w-full my-4 shadow-lg border-solid border border-gray-100">
                <img className="rounded-t-md bg-center bg-no-repeat  bg-cover " src={strDrinkThumb} alt={strDrink} />
                <div className="body px-4  ">
                    <p className="font-semibold text-lg tracking-wider text-blue-900" >
                        {strDrink}
                    </p>
                    <div>
                        <button className="text-pink-500">See the recipe</button> 
                    </div>
                </div>
     
            </CardBody>
        </div>
     );
}
 
export default Drink;